        </transition-group>
        <div class="section1-content">
          <p class="name">你好，我叫陶俊逸</p>
          <p class="desc">我是一名前端工程师,自学前端开发半年，乐于尝试新技术，学习能力强。</p>
        </div>
      </div>
      <div class="nav" :class="{'fix':fixed}" id="nav">
        <ul class="nav-fat" v-show="screenWidth >768">
          <li :class="{'active': activeSection === 'home'}"><a href="javascript:;" @click="goToSection('#home')">首页</a></li>
          <li :class="{'active': activeSection === 'aboutMe'}"><a href="javascript:;" @click="goToSection('#aboutMe')">关于我</a></li>
          <li :class="{'active': activeSection === 'skills'}"><a href="javascript:;" @click="goToSection('#skills')">技能</a></li>
          <li :class="{'active': activeSection === 'contact'}"><a href="javascript:;" @click="goToSection('#contact')">联系我</a></li>
        </ul>
        <div v-show="screenWidth <=768" class="nav-thin">
          <div class="thin-navheader-container">
            <div class="nav-title">陶俊逸的简历</div>
            <i class="iconfont icon-list" :class="{'active': toggleNavBar}" @click="showBar"></i>
          </div>
          <transition name="navlist">
            <ul v-show="toggleNavBar">
              <li :class="{'active': activeSection === 'home'}"><a href="javascript:;" @click="goToSection('#home')">首页</a></li>
              <li :class="{'active': activeSection === 'aboutMe'}"><a href="javascript:;" @click="goToSection('#aboutMe')">关于我</a></li>
              <li :class="{'active': activeSection === 'skills'}"><a href="javascript:;" @click="goToSection('#skills')">技能</a></li>
              <li :class="{'active': activeSection === 'contact'}"><a href="javascript:;" @click="goToSection('#contact')">联系我</a></li>
            </ul>
          </transition>
        </div>
      </div>
      <div class="section2" id="aboutMe">
        <div class="section-content">
          <h1 class="content-header">关于我</h1>
          <ul>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-man"></i>
              </div>
              <p>25岁</p>
            </li>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-xueli"></i>
              </div>
              <p>本科</p>
            </li>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-locationicon"></i>
              </div>
              <p>重庆</p>
            </li>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-clock"></i>
              </div>
              <p>寻找机遇</p>
            </li>
          </ul>
          <p class="content-desc">
            我叫陶俊逸，男，25岁，2017年毕业于西南财经大学税收专业，2017/7-2019/5于四川天健税务师事务所从事财务工作，后因兴趣转行，于2019/12自学前端至今。
          </p>
          <p class="content-desc">
            有着不错的html+css+js基础，并且一直对学习新技术保持热情。我能够熟练使用PS+Markman对设计图标注，再结合HTML+CSS进行页面搭建；
            熟练掌握JS语法、事件、定时器的基本使用，实现页面的交互；能够使用Vue基础功能，结合axios\Vuex\VueRouter完成项目；
            了解移动端的布局，完成过仿网易云音乐项目；了解Node+ajax前后端交互；了解正则表达式、es6、JQuery、Bootstrap的基本用法。
            <a href="https://github.com/Taojunyi729" target="_blank">前往我的GitHub</a>
          </p>
        </div>
      </div>
      <div class="section3" id="skills">
        <div class="section-content">
          <h1 class="content-header">技能</h1>
          <p class="content-desc">
            乐于学习分享，每天保持进步。在我的GitHub上面有学习中完成的Demo；经过6月通过2天的查询与模仿，利用Github pages完成了这个个人主页的搭建。
            正在阅读中的书籍是《JavaScript权威指南第6版》；7-8月份的学习计划是复习1-6月的基础学习，增强项目练习，计划完成10个前端练习项目。
          </p>
          <ul class="content-skill">
            <li>HTML</li>
            <li>CSS</li>
            <li>JS</li>
            <li>Vue</li>
            <li>Less</li>
            <li>ES6</li>
            <li>WebPack</li>
            <li>NodeJS+ajax</li>
            <li>Jquery</li>
            <li>BootStrap</li>
            <li>Github</li>
          </ul>
        </div>
      </div>
      <div class="section4" id="contact">
        <div class="section-content">
          <h1 class="content-header">联系我</h1>
          <ul>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-tel"></i>
              </div>
              <p>Tel</p>
              <p class="small">18349112511</p>
            </li>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-qq"></i>
              </div>
              <p>qq</p>
              <p class="small">764728783</p>
            </li>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-wechat"></i>
              </div>
              <p>wechat</p>
              <p class="small">A764728684</p>
            </li>
            <li class="content-icon">
              <div class="icon">
                <i class="iconfont icon-185078emailmailstreamline"></i>
              </div>
              <p>Email</p>
              <p class="small">764728783@qq.com</p>
            </li>
          </ul>
        </div>
      </div>
      <div class="footer"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      activeBanner: 0,
      bannerData: [
        {img: require('../assets/images/header-bg1.jpg')},
        {img: require('../assets/images/header-bg2.jpg')},
        {img: require('../assets/images/header-bg3.jpg')},
        {img: require('../assets/images/header-bg4.jpg')}],
      screenWidth: document.body.clientWidth,
      toggleNavBar: false,
      activeSection: 'home',
      fixed: false
    }
  },
  mounted () {
    const that = this
    this.initBanner()
    window.onresize = () => {
      that.screenWidth = document.body.clientWidth
    }
    window.addEventListener('scroll', () => {
      this.scrollTop()
      this.locateSection()
    })
  },
  methods: {
    initBanner () {
      setInterval(() => {
        this.activeBanner++
        this.activeBanner = this.activeBanner % 4
      }, 3000)
    },
    goToSection (tar) {
      let target = document.querySelector(tar)
      target.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
        inline: 'nearest'
      })
    },
    showBar () {
      this.toggleNavBar = !this.toggleNavBar
    },
    scrollTop () {
      var scrollT = document.documentElement.scrollTop
      const homeHeight = document.querySelector('#home').offsetHeight
      const navHeight = document.querySelector('#nav').offsetHeight
      const aboutMe = document.querySelector('#aboutMe')
      if (scrollT > homeHeight) {
        this.fixed = true
        aboutMe.style.marginTop = `${navHeight}px`
      } else {
        this.fixed = false
        aboutMe.style.marginTop = 0
      }
    },
    locateSection () {
      const scrollT = document.documentElement.scrollTop
      const halfClientH = document.documentElement.clientHeight / 2
      const aboutMeScrollT = document.querySelector('#aboutMe').offsetTop
      const skillsScrollT = document.querySelector('#skills').offsetTop
      const contactScrollT = document.querySelector('#contact').offsetTop
      if (skillsScrollT - scrollT >= halfClientH && aboutMeScrollT - scrollT < halfClientH) {
        this.activeSection = 'aboutMe'
      } else if (contactScrollT - scrollT >= halfClientH && skillsScrollT - scrollT < halfClientH) {
        this.activeSection = 'skills'
      } else if (contactScrollT - scrollT < halfClientH) {
        this.activeSection = 'contact'
      } else {
        this.activeSection = 'home'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.section-content{
  text-align:center;
  padding:100px 0 60px;
  margin:0 auto;

  .content-header{
    display:inline-block;
    font-size:40px;
    padding-bottom:12px;
    border-bottom:6px solid #ef7674;
    font-weight:400;
  }

  .content-icon{
    display: inline-block;
    margin:60px 0;
    padding:0 50px;
    text-align: center;

    .icon{
      display: inline-block;
      width:100px;
      height:100px;
      border-radius: 50%;
      background-color: #ef7674;
      line-height:100px;
      color:#fff;
      margin-bottom:25px;

      i{
        font-size:50px;
      }
    }

    p{
      font-size:18px;

      &.small{
        font-size:14px;
        color:#ef7674;
        margin-top:10px;
      }
    }
  }

  .content-desc{
    max-width:1100px;
    padding:0 25px;
    text-align: left;
    text-indent:36px;
    font-size:18px;
    line-height:1.5;
    color:#897f8a;
    margin:0 auto 10px;
  }

}
/*首页*/
.section1{
  width:100%;
  height:100vh;
  position:relative;

  .section1-banner{
    width:100%;
    height:100vh;

    li{
      width:100%;
      height:100vh;
      background-size:cover;
      background-position:left top;
      overflow:hidden;
    }
  }

  .section1-content{
    position:absolute;
    top:35%;
    left:0;
    right:0;
    text-align:center;
    color:#fff;
    padding:0 15px;

    .name{
      font-size:47px;
      margin-bottom:27px;
      font-weight:800;
      padding:0 50px;
    }

    .desc{
      font-size:20px;
      line-height:1.5;
      letter-spacing:5px;
    }
  }
}

.banner-enter{
  opacity:0.8;
}

.banner-enter-active{
  transition: all .5s ease;
}
/*导航*/
.nav{
  color:#777777;
  font-size:20px;
  background-color: #fff;
  font-weight:700;
  width:100%;

  &.fix{
    position:fixed;
    top:0;
    left:0;
    z-index:99;
  }

  .nav-fat{
    margin:0 auto;
    width:500px;

    li{
      display:inline-block;
      padding:40px 30px;

      &.active{
        color:#ef7674;
      }
    }
  }

  .nav-thin{

    .thin-navheader-container{
      height:50px;
      line-height:50px;
      padding:0 5%;

      &:after{
        content:'';
        display:block;
        clear:both;
      }

      .nav-title{
        float:left;
      }

      i{
        display:inline-block;
        width:35px;
        height:28px;
        text-align: center;
        line-height:28px;
        border:2PX solid #e7e7e7;
        border-radius:10px;
        font-size:20px;
        margin-top:10px;
        float:right;

        &.active{
          background-color: #e7e7e7;
        }
      }
    }

    ul{
      padding:10px 0;
      height:232px;
      border-top:1PX solid #e7e7e7;

      li{
        padding:15px 0;
        text-align: center;

        &.active{
          color:#ef7674;
        }
      }
    }
  }
}

.navlist-enter{
  height:0;
  opacity:0;
}

.navlist-enter-active{
  transition:all 1s ease;
}
/*关于我*/
.section2{

  a{
    display:block;
    color:#337ab7;
    font-size:12px;
  }
}
/*技能*/
.section3{
  background-color: #fff;

  .content-skill{
    text-align:left;
    margin:40px auto 0;
    font-size:0;
    max-width:1100px;
    padding:0 25px;

    li{
      text-align: left;
      font-size:17px;
      font-weight:600;
      color:#604c5c;
      display: inline-block;
      margin-bottom:10px;
      width:33%;
      min-width:115px;
    }
  }
}
/*底部*/
.footer{
  width:100%;
  height:10vh;
  background-color:#263238;
}
</style>



